@import "styles/variables.scss";

body {
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4 {
  font-family: Poppins;
}

a {
  color: rgb(var(--color-1));
  text-decoration: none;
  border: 0;
  font-weight: 700;

  &:hover {
    color: black;
    border-bottom: 1px solid black;
  }
}
